import React from 'react';
import './jsonfy.less';
import shouqi from '../assets/shouqi.png';
import zhankai from '../assets/zhankai.png';

export default class JsonFY extends React.Component {

  constructor() {
    super();
    this.state = {
      result: null
    }
    this.createStructure = this.createStructure.bind(this);
    this.toggle = this.toggle.bind(this);
  }

  render() {
    return <div className="jsonfy">
      <div className="title">JSON格式化、美化</div>
      <div className="container">
        <div className="left">
          <textarea placeholder="输入区域." className="input" ref="input" onKeyDown={this.onKeyDown}></textarea>
          <div className="btns">
            <div className="btn" onClick={() => this.beauty()}>美化(回车)</div>
            <div className="btn" onClick={() => this.showLayer()}>展示层级</div>
          </div>
        </div>
        <div className="right">
          <textarea placeholder="输出区域" disabled className="output" ref="output"></textarea>
          <div ref="tree">
            {this.state.result && this.createStructure(this.state.result)}
          </div>
        </div>
      </div>
    </div>
  }

  onKeyDown = (e) => {
    if (e.keyCode === 13) {
      this.beauty();
    }
  }

  beauty = () => {
    let val = this.refs.input.value
    let result
    try {
      result = JSON.parse(val, null, 2)
    } catch (e) {
      window.alert('非法json串')
      return
    }
    if (result) {
      this.refs.output.style.display = 'block'
      this.refs.tree.style.display = 'none'
      this.refs.output.value = JSON.stringify(result, null, 2)
    }
  }

  showLayer = () => {
    let val = this.refs.input.value
    let result
    try {
      result = JSON.parse(val, null, 2)
    } catch (e) {
      window.alert('非法json串')
      return
    }
    if (result) {
      this.refs.output.style.display = 'none'
      this.refs.tree.style.display = 'block'
      this.setState({
        result: result
      })
    }
  }

  // 生成结构
  createStructure = (data) => {
    if (Array.isArray(data)) { // 数组
      return data.map((item) => {
        return this.createStructure(item)
      })
    } else { 
      if (typeof data === 'object') {// 对象
        return Object.keys(data).map((key, index) => {
          return this.createDom(key, data[key])
        })
      } else { // 其他简单值
        return <div key={data}>{data}</div>
      }
    }
  }

  // 对象生成渲染结构
  createDom = (key, val) => {
    if (typeof val === 'object') { // 值是复杂结构
      return this.folderDom(key, val)
    } else { // 值是简单结构
      return this.simpleDom(key, val)
    }
  }


  // 单一的折叠结构[值为对象]
  folderDom = (key, value) => {
    return (
      <div key={key} className="tree-item folder-item">
        <div className="folder-title" onClick={(e) => this.toggle(e)}>
          <img src={zhankai}  /> <span>{key}:</span>
        </div>
        <div className="folder-content" style={{ display: 'block' }}>{value && this.createStructure(value)}</div>
      </div>
    )
  }

  // 单一的平铺结构[值不为对象]
  simpleDom = (key, value) => {
    return <div key={key} className="tree-item folder-item">
        <div className="folder-title" onClick={(e) => this.toggle(e)}>
          <img src={zhankai}  /> <span>{key}:</span>
        </div>
        <div className="folder-content" style={{ display: 'block' }}>{value}</div>
    </div>
  }


  // 折叠切换
  toggle = (e) => {
    console.log('click')
    let target = e.target;
    while(target.tagName != 'DIV') {
      target = target.parentElement
    }
    let preStyleDisplay = target.nextElementSibling.style.display
    target.nextElementSibling.style.display = preStyleDisplay === 'none' ? 'block' : 'none'
    target.firstElementChild.src = preStyleDisplay === 'none' ? zhankai : shouqi
  }


}


