import React from 'react';
import PropTypes from 'prop-types';
import './app.less';
export default class App extends React.Component{

  static contextTypes = {
    router: PropTypes.object.isRequired,
  }

   render() {
     return <div className="app">
        <h3>在线工具入口</h3>
        <div>风格：低调、不奢华、实用</div>
        <h5>入口：</h5>
       <div className="entry-item" onClick={() => this.link('/book')}>1、备忘录</div>
       <div className="entry-item" onClick={() => this.link('/json')}>2、json美化</div>
     </div>
   }

   link = (way) => {
     this.context.router.history.push(way);
   }
}


